	module.exports = {"data":{"markdownRemark":{"html":"<p>凌晨读《走下圣坛的周恩来》，作者的结论是周恩来不是帅才。似乎作者认为这是突破。现在“圣坛”或“神坛”盛行，都是名人走下了，不知什么时候会写走土什么的。现实生活中总是会有人走上“神坛”的。</p>\n<p>写《93中国发展报告》政治的总论部分，将1993年政治发展归纳为“开发推动社会主义市场经济的政治资源”，一个新想法，要好好论证，以后可写专文论述。中国经济发展和现代化推动．籍要大量政治资源，包括制度资源、政策资源等。西方国家的发展，今天已经有所不同，需要更多的经济和信息资源，而不是政治资源。中国国情决定需要富集的政治资源。又提出“经济化政治”的概念。估计在很长的一段时期......</p>","id":"/home/swain/workspace/political-life/src/pages/cn/0004.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/cn/0004.md","frontmatter":{"title":"1月7日 星期五"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/cn/0004.md absPath of file >>> MarkdownRemark"}}

